.theme-light {
    background: hsl(0, 0%, 100%);

    .bg-top {
        background-color: hsl(225, 100%, 98%);
    }

    .bg-card {
        background: hsl(227, 47%, 96%)
    }
    .bg-card:hover {
        background: rgb(216, 218, 224)
    }

    .text-accent {
        color: hsl(228, 12%, 44%);
    }

    .text-primary {
        color: hsl(230, 17%, 14%);
    }

    .slider {
        background: hsl(230, 22%, 74%);
    }

    .slider:before {
        background: hsl(225, 100%, 98%);
    }
}

.theme-dark {
    background: hsl(230, 17%, 14%);

    .bg-top {
        background-color: hsl(232, 19%, 15%);
    }

    .bg-card {
        background: hsl(228, 28%, 20%);
    }

    .bg-card:hover {
        background: hsl(229, 28%, 28%);
    }

    .text-accent {
        color: hsl(228, 34%, 66%);
    }

    .text-primary {
        color: hsl(0, 0%, 100%);
    }
    .slider {
        background: linear-gradient(to right, hsl(210, 78%, 56%),hsl(146, 68%, 55%));
    }

    .slider:before {
        background: hsl(232, 19%, 15%);
    }
}

.text-bad {
    color: hsl(356, 69%, 56%);
}
.text-good {
    color: hsl(163, 72%, 41%);
}

@font-face {
    font-family: 'Inter';
    src: url("https://fonts.google.com/specimen/Inter");
}

html {
    margin: 0;
    padding: 0;
    font-size: 14px;
}

body {
    margin: 0;
    padding: 0;
    padding-bottom: 2em;
}

header {
    padding-top: 2em;
    height: 210px;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
}

main {
    position: relative;
    top: -55px;
}

.container {
    margin: 0 1.6rem;
    max-width: 1200px;
}

.mb-0 {
    margin-bottom: 0;
}

.mb-3 {
    margin-bottom: 1.6rem;
}

.mt-3 {
    margin-top: 1.6rem;
}

img {
    display: block;
    margin: 0;
}

.header__border-bottom {
    border-bottom: 1px solid #000;
}

/* Typograph */
html {
    font-family: "Inter";
}

h1, h2, h3, p {
    margin: 0;
}

h1 {
    font-size: 1.8rem;
    padding-bottom: 0.15em;
    margin-bottom: 0.25em;
    font-weight: 700;
}

.subtitle {
    font-size: 1.2rem;
    font-weight: 700;
}

/* sections */
.social-totals {
    display: flex;
    flex-direction: column;
}

.social-total__card {
    border-radius: 0.5rem;
    padding: 1.5rem 0;
    margin-bottom: 1.5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    min-height: 200px;
}

.social-total__title {
    display: inline-flex;
    align-items: center;
}

.social-total__total {
    font-size: 4rem;
    font-weight: 700;

    p {
        font-size: 1rem;
        text-transform: uppercase;
        letter-spacing: 0.5rem;
        font-weight: 400;
    }
}

.social-total__today-change {
    display: inline-flex;
    align-items: center;
    p {
        font-weight: 700;
    }
}

.social-stat__card {
    border-radius: 0.5rem;
    padding: 1.8rem;
    margin-bottom: 1.5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 85px;
}

.social-stat__row {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
}

.social-stat__total {
    font-size: 2.5rem;
    font-weight: 700;
}

.social-stat__change {
    display: inline-flex;
    align-items: center;
    p {
        font-weight: 700;
    }
}

.fb-border-top,
.twitter-border-top,
.insta-border-top,
.yt-border-top {
    position: relative;
    height: 5px;
    width: 100%;
    top: -1.5rem;
    border-top-left-radius: 1rem;
    border-top-right-radius: 1rem;
}

.fb-border-top {
    background: hsl(208, 92%, 53%);
}

.twitter-border-top {
    background: hsl(203, 89%, 53%);
}

.insta-border-top {
    background-image: linear-gradient(to right, hsl(37, 97%, 70%),hsl(329, 70%, 58%));
}

.yt-border-top {
    background: hsl(348, 97%, 39%);
}

.icon {
    width: 1.6rem;
    margin-right: 0.5em;
}

.arrow {
 width: 0.75rem;
 height: 0.5rem;
 margin-right: 0.5em;
}

.handle {
    font-weight: 700;
}

/* TOGGLE */
.theme {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 30px;
}
.switch {
    position: relative;
    display: inline-block;
    width: 48px;
    height: 25px;
}

.switch input { 
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    -webkit-transition: .4s;
    transition: .4s;
}

.slider:before {
    position: absolute;
    content: "";
    height: 18px;
    width: 18px;
    right: 4px;
    bottom: 4px;
    -webkit-transition: .4s;
    transition: .4s;
}

input:checked + .slider {
    background-color: #2196F3;
}

input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
    -webkit-transform: translateX(-22px);
    -ms-transform: translateX(-22px);
    transform: translateX(-22px);
}

/* Rounded sliders */
.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}

@media screen and (min-width: 650px) {
    .header__border-bottom {
        border-bottom: 0px solid #000;
    }
    .header__row {
        display: flex;
        justify-content: space-between;
    }

    .theme {
        .subtitle {
            margin-right: 0.65em;
        }
    }

    main {
        top: -100px;
    }

    .social-totals, .social-stats {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-evenly;
    }

    .social-total__card {
        width: 275px;
    }

    .social-stat__card {
        width: 225px;
    }
}

/* LRG DESKTOP */
@media screen and (min-width: 1223px) {
    .container {
        margin: 0 auto;
    }
    .social-totals, .social-stats {
        justify-content: space-between;
    }
}